html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}
body {
	line-height: 1;
}
ol,
ul {
	list-style: none;
}
blockquote,
q {
	quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
	content: "";
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* Begin own css */

html {
	height: 100%;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	color: black;
}

body {
	height: 100%;
	background-image: url(https://external-preview.redd.it/5XNzxTrtqfyb44L7SIJ69DG2bYV8um8Q41lx5nt4ug4.png?auto=webp&s=59065e00b9a6706716bc897d65910c762812a2ac);
	background-repeat: no-repeat;
	background-size: 105% 105%;
	overflow: hidden;
}

p,
input {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	appearance: none;
}

input {
	border: 1px solid black;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.App {
	display: flex;
	flex-direction: column;
	border: 1px solid black;
	border-radius: 10px;
	padding: 12px;
	width: 90%;
	height: 90%;
	max-width: 800px;
	min-height: 250px;
	max-height: 300px;
	margin-bottom: 23px;
	background-color: white;
}

@media (max-width: 1023px) {
	.App {
		height: 70%;
		min-height: 300px;
		max-height: 600px;
	}
}

/* Search Bar Stuff */

#searchcontainer {
	display: flex;
	justify-content: flex-end;
	height: 40px;
	width: 100%;
	box-sizing: border-box;
}

form {
	flex: 1;
}

#weather-api-search {
	flex: 1;
	height: 40px;
	font-size: 18px;
	width: 100%;
}

#country {
	width: 5ch;
	height: 40px;
	font-size: 18px;
}

.searchbutton {
	background-color: rgb(97, 185, 226);
	border: 1px solid rgb(175, 219, 240);
	box-sizing: border-box;
	border-radius: 3px;
	font-size: 18px;
}

#locationbutton {
	height: 40px;
	width: 6ch;
	text-align: center;
}

#submit {
	height: 40px;
	width: 9ch;
	align-self: flex-end;
}

form {
	display: flex;
	width: 90%;
	height: 40px;
}

/* Display Stuff */
#displaycontainer {
	display: flex;
	border-top: 1px solid black;
	margin-top: 10px;
	flex: 1 1 400;
	height: 100%;
	box-sizing: border-box;
}

@media (max-width: 1023px) {
	#displaycontainer {
		flex-direction: column;
	}
}

#weathercontainer {
	display: flex;
	flex: 1 1 70%;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	width: 70%;
	min-height: 40px;
	height: 100%;
	padding: 20px;
	box-sizing: border-box;
}

@media (max-width: 1023px) {
	#weathercontainer {
		padding: 12px;
		flex-basis: 50%;
		width: 100%;
	}
}

#haikucontainer {
	padding: 12px;
	padding-left: 24px;
	flex: 1 1 30%;
	box-sizing: border-box;
	border-left: 1px solid black;
}

@media (max-width: 1023px) {
	#haikucontainer {
		padding-left: 12px;
		flex-basis: 50%;
		width: 100%;
		border-left-width: 0px;
		border-top: 1px solid grey;
	}
}

#location {
	font-size: 25px;
	flex: 1 1 15%;
}

#temp {
	font-size: 25px;
	flex: 1 1 15%;
	text-align: end;
}

@media (max-width: 767px) {
	#location, #temp {
		font-size: 21px;
	}
}

#emoji {
	font-size: xx-large;
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.weatherdiv {
	font-size: large;
	line-height: 30px;
}

@media (max-width: 767px) {
	.weatherdiv {
		line-height: 20px;
		font-size: small;
	}
}

.greyed {
	color: lightgrey;
}

.fine {
	color: black;
}

/* Haiku */

#bonushaiku {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

@media (max-width: 1023px) {
	#bonushaiku {
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}
}

#haikutext {
	line-height: 25px;
	font-size: 14px;
}

@media (max-width: 767px) {
	#haikutext {
		line-height: 30px;
	}
}

#haikumetadata {
	font-size: 12px;
	line-height: 20px;
}

@media (max-width: 767px) {
	#haikumetadata {
		font-size: xx-small;
		width: 16ch;
		line-height: 15px;
	}
}

/* General */

.reveal {
	opacity: 1;
	animation-name: show;
	animation-duration: 0.75s;
}

@keyframes show {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.hide {
	opacity: 0%;
}

#credit {
	position: absolute;
	bottom: 1%;
	right: 2%;
	font-size: 0.7rem;
}

@media (max-width: 1023px) {
	#credit {
		width: 100%;
		text-align: center;
		margin-left: auto;
		margin-right: auto;
	}
}
